html,
body {
	height: 100%;
	padding: 0;
	overflow: hidden;
    background-color: blue;
}
/* App.js의 css */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




/* Nav.js의 css */

.nav{
  
}
.nav-trans{
  position: absolute;
  top: 50%;
  transform: rotateZ(-90deg);
  transform-origin: 0 50%;
  
}
.nav-trans-right{
  position: absolute;
  top: 50%;
  transform: rotateZ(-90deg);
  transform-origin: 50% 0;
}

.nav-left{
  left: 20px;
}
.nav-right{
  right: -40px;
}


/* Wrapper.js의 css */

html.prevent-history,
html.prevent-history body {
	overscroll-behavior: contain;
}

.scroll-wrapper {
	overflow: hidden;
}

ul {
	display: block;
	white-space: nowrap;
	height: 300px;
	overflow: visible;
	padding: 0;
	font-size: 0;
	transform: translateX(0px);
}

li {
	box-sizing: border-box;
	height: 300px;
	width: 300px;
	display: inline-block;
	border: 1px solid;
	margin-right: 5px;
}

li:nth-child(4n) {
	background: #B4B4B4;
}

li:nth-child(4n + 1) {
	background: #2D2D2D;
}

li:nth-child(4n + 2) {
	background: #857D68;
}

li:nth-child(4n + 3) {
	background: #F5F5F5;
}